import {
  Flex,
  Heading,
  Text,
  useBreakpointValue,
  Button,
  Card,
  HStack,
  VStack,
} from '@chakra-ui/react';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { detectDevice } from '../utils/deviceDetection';
import { useColorModeValue } from './ui/color-mode';

const Download: React.FC = () => {
  const { t } = useTranslation();
  const isBaseSize = useBreakpointValue(
    { base: true, md: false },
    { ssr: false }
  );
  const cardBg = useColorModeValue('#E9F3DA', '#1e2210');
  const borderColor = useColorModeValue('#b8c8b5', '#9ca76e');
  const textColor = useColorModeValue('gray.700', 'gray.300');
  const desktopTextColor = useColorModeValue('gray.600', 'gray.400');
  const placeholderBg = useColorModeValue('gray.200', 'gray.700');
  const placeholderBorderColor = useColorModeValue('gray.300', 'gray.600');
  const placeholderTextColor = useColorModeValue('gray.500', 'gray.400');
  const device = detectDevice();

  return (
    <Flex
      id="download"
      w="100%"
      alignItems="center"
      justifyContent="center"
      px={isBaseSize ? 4 : 12}
      py={16}
      direction="column"
      gap={8}
    >
      <Heading
        as="h2"
        size={isBaseSize ? 'xl' : '2xl'}
        fontWeight="bold"
        color="brand.500"
        textAlign="center"
      >
        {t('download.title')}
      </Heading>
      <Text
        fontSize={isBaseSize ? 'md' : 'lg'}
        textAlign="center"
        color={textColor}
        maxW="600px"
      >
        {t('download.subtitle')}
      </Text>
      <Card.Root
        bg={cardBg}
        border="2px solid"
        borderColor={borderColor}
        borderRadius="xl"
        w="100%"
        maxW="600px"
      >
        <Card.Body>
          <VStack gap={4}>
            {(device === 'ios' || device === 'desktop') && (
              <Button
                as="a"
                href="#"
                colorScheme="brand"
                size="lg"
                w="100%"
                isDisabled
              >
                {t('download.appStore')}
              </Button>
            )}
            {(device === 'android' || device === 'desktop') && (
              <Button
                as="a"
                href="#"
                colorScheme="brand"
                size="lg"
                w="100%"
                isDisabled
              >
                {t('download.playStore')}
              </Button>
            )}
            {device === 'desktop' && (
              <Text fontSize="sm" color={desktopTextColor} textAlign="center">
                {t('download.bothStores')}
              </Text>
            )}
          </VStack>
        </Card.Body>
      </Card.Root>
      {!isBaseSize && (
        <Flex
          w="300px"
          h="600px"
          bg={placeholderBg}
          borderRadius="lg"
          alignItems="center"
          justifyContent="center"
          border="2px dashed"
          borderColor={placeholderBorderColor}
        >
          <Text color={placeholderTextColor} fontSize="sm" textAlign="center">
            Phone mockup placeholder
            <br />
            (Description: iPhone showing Drill Drop app)
          </Text>
        </Flex>
      )}
    </Flex>
  );
};

export default Download;

