import {
  Flex,
  Heading,
  Text,
  useBreakpointValue,
  SimpleGrid,
  Card,
  Box,
} from '@chakra-ui/react';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useColorModeValue } from './ui/color-mode';

const UseCases: React.FC = () => {
  const { t } = useTranslation();
  const isBaseSize = useBreakpointValue(
    { base: true, md: false },
    { ssr: false }
  );
  const cardBg = useColorModeValue('#E9F3DA', '#1e2210');
  const borderColor = useColorModeValue('#b8c8b5', '#9ca76e');
  const textColor = useColorModeValue('gray.700', 'gray.300');
  const titleColor = useColorModeValue('gray.800', 'gray.200');

  const useCases = [
    {
      key: 'ultraRunner',
      icon: '',
    },
    {
      key: 'firstResponder',
      icon: '',
    },
  ];

  return (
    <Flex
      id="use-cases"
      w="100%"
      alignItems="center"
      justifyContent="center"
      px={isBaseSize ? 4 : 12}
      py={16}
      direction="column"
      gap={8}
    >
      <Heading
        as="h2"
        size={isBaseSize ? 'xl' : '2xl'}
        fontWeight="bold"
        color="brand.500"
        textAlign="center"
      >
        {t('useCases.title')}
      </Heading>
      <Text
        fontSize={isBaseSize ? 'md' : 'lg'}
        textAlign="center"
        color={textColor}
        maxW="800px"
      >
        {t('useCases.subtitle')}
      </Text>
      <SimpleGrid
        columns={isBaseSize ? 1 : 2}
        gap={6}
        w="100%"
        maxW="1000px"
      >
        {useCases.map((useCase) => (
          <Card.Root
            key={useCase.key}
            bg={cardBg}
            border="2px solid"
            borderColor={borderColor}
            borderRadius="xl"
          >
            <Card.Body>
              <Box fontSize="4xl" mb={4} textAlign="center">
                {useCase.icon}
              </Box>
              <Heading
                as="h3"
                size="md"
                fontWeight="bold"
                mb={2}
                color={titleColor}
                textAlign="center"
              >
                {t(`useCases.${useCase.key}.title`)}
              </Heading>
              <Text
                fontSize="sm"
                color={textColor}
                textAlign="center"
              >
                {t(`useCases.${useCase.key}.description`)}
              </Text>
            </Card.Body>
          </Card.Root>
        ))}
      </SimpleGrid>
    </Flex>
  );
};

export default UseCases;

