import {
  Flex,
  Heading,
  Text,
  useBreakpointValue,
} from '@chakra-ui/react';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useColorModeValue } from './ui/color-mode';

const Description: React.FC = () => {
  const { t } = useTranslation();
  const isBaseSize = useBreakpointValue(
    { base: true, md: false },
    { ssr: false }
  );
  const textColor = useColorModeValue('gray.700', 'gray.300');
  const placeholderBg = useColorModeValue('gray.200', 'gray.700');
  const placeholderBorderColor = useColorModeValue('gray.300', 'gray.600');
  const placeholderTextColor = useColorModeValue('gray.500', 'gray.400');

  return (
    <Flex
      w="100%"
      alignItems="center"
      justifyContent="center"
      px={isBaseSize ? 4 : 12}
      py={16}
      direction={isBaseSize ? 'column' : 'row'}
      gap={8}
    >
      <Flex
        direction="column"
        gap={4}
        maxW={isBaseSize ? '100%' : '50%'}
        textAlign={isBaseSize ? 'center' : 'left'}
      >
        <Heading
          as="h2"
          size={isBaseSize ? 'xl' : '2xl'}
          fontWeight="bold"
          color="brand.500"
        >
          {t('description.title')}
        </Heading>
        <Text
          fontSize={isBaseSize ? 'md' : 'lg'}
          color={textColor}
        >
          {t('description.text')}
        </Text>
      </Flex>
      {!isBaseSize && (
        <Flex
          w="50%"
          h="400px"
          bg={placeholderBg}
          borderRadius="lg"
          alignItems="center"
          justifyContent="center"
          border="2px dashed"
          borderColor={placeholderBorderColor}
        >
          <Text color={placeholderTextColor} fontSize="sm">
            iPhone mockup placeholder
            <br />
            (Description: iPhone mockup showing Drill Drop app interface with 80s color scheme)
          </Text>
        </Flex>
      )}
    </Flex>
  );
};

export default Description;

