import {
  Flex,
  Link,
  Text,
  useBreakpointValue,
  Menu,
  Portal,
  IconButton,
  Button,
} from '@chakra-ui/react';
import { useTranslation } from 'react-i18next';
import { LuChevronDown, LuMoon, LuSun } from 'react-icons/lu';
import React from 'react';
import type { SupportedLocale } from '../i18n';
import { useColorMode, useColorModeValue } from './ui/color-mode';

const Header: React.FC = () => {
  const { t, i18n } = useTranslation();
  const { colorMode, toggleColorMode } = useColorMode();
  const bgColor = useColorModeValue('#E9F3DA', '#1e2210');
  const borderColor = useColorModeValue('#b8c8b5', '#9ca76e');

  const isBaseSize = useBreakpointValue(
    { base: true, md: false },
    { ssr: false }
  );

  const languages: Array<{ code: SupportedLocale; name: string }> = [
    { code: 'en', name: 'English' },
    { code: 'fr', name: 'Franais' },
    { code: 'zh-CN', name: '' },
    { code: 'pt-BR', name: 'Portugus (Brasil)' },
    { code: 'de', name: 'Deutsch' },
    { code: 'it', name: 'Italiano' },
    { code: 'ja', name: '' },
    { code: 'ko', name: '' },
    { code: 'es', name: 'Espaol' },
    { code: 'id', name: 'Bahasa Indonesia' },
    { code: 'tr', name: 'Trke' },
  ];

  const currentLanguage = languages.find((lang) => lang.code === i18n.language) || languages[0];

  const changeLanguage = (locale: SupportedLocale) => {
    i18n.changeLanguage(locale);
  };

  return (
    <Flex
      w="100%"
      py={4}
      px={isBaseSize ? 4 : 12}
      gap={4}
      alignItems="center"
      justifyContent={isBaseSize ? 'space-between' : 'space-between'}
      bg={bgColor}
      borderBottom="1px solid"
      borderColor={borderColor}
      position="sticky"
      top={0}
      zIndex={1000}
    >
      <Link href="/" _hover={{ textDecoration: 'none' }}>
        <Text fontSize="2xl" fontWeight="bold" color="brand.500">
          {t('header.appName')}
        </Text>
      </Link>
      {!isBaseSize && (
        <Flex alignItems="center" gap={4}>
          <Link href="#features" fontWeight="medium">
            {t('header.features')}
          </Link>
          <Link href="#terms" fontWeight="medium">
            {t('header.terms')}
          </Link>
        </Flex>
      )}
      <Flex alignItems="center" gap={isBaseSize ? 1 : 2}>
        <Menu.Root>
          <Menu.Trigger asChild>
            <Button
              variant="ghost"
              size={isBaseSize ? 'xs' : 'sm'}
              fontSize={isBaseSize ? 'xs' : 'sm'}
            >
              <Flex alignItems="center" gap={1}>
                {isBaseSize ? currentLanguage.code.toUpperCase() : currentLanguage.name}
                <LuChevronDown />
              </Flex>
            </Button>
          </Menu.Trigger>
          <Portal>
            <Menu.Positioner>
              <Menu.Content>
                {languages.map((lang) => (
                  <Menu.Item
                    key={lang.code}
                    value={lang.code}
                    onClick={() => {
                      if (i18n.language !== lang.code) {
                        changeLanguage(lang.code);
                      }
                    }}
                    opacity={i18n.language === lang.code ? 0.5 : 1}
                    cursor={i18n.language === lang.code ? 'default' : 'pointer'}
                  >
                    {lang.name}
                  </Menu.Item>
                ))}
              </Menu.Content>
            </Menu.Positioner>
          </Portal>
        </Menu.Root>
        <IconButton
          aria-label="Toggle color mode"
          onClick={toggleColorMode}
          variant="ghost"
          size="sm"
        >
          {colorMode === 'light' ? <LuMoon /> : <LuSun />}
        </IconButton>
      </Flex>
    </Flex>
  );
};

export default Header;

