import {
  Flex,
  Heading,
  Text,
  useBreakpointValue,
  VStack,
  Box,
} from '@chakra-ui/react';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useColorModeValue } from './ui/color-mode';

const TermsOfService: React.FC = () => {
  const { t } = useTranslation();
  const isBaseSize = useBreakpointValue(
    { base: true, md: false },
    { ssr: false }
  );
  const lastUpdatedColor = useColorModeValue('gray.600', 'gray.400');
  const textColor = useColorModeValue('gray.700', 'gray.300');

  const sections = [
    'introduction',
    'noPII',
    'noCloud',
    'noNetwork',
    'noAnalytics',
    'gdpr',
    'injury',
    'userResponsibility',
    'noWarranty',
    'changes',
  ];

  return (
    <Flex
      id="terms"
      w="100%"
      alignItems="center"
      justifyContent="center"
      px={isBaseSize ? 4 : 12}
      py={16}
      direction="column"
      gap={8}
    >
      <VStack gap={4} maxW="1000px" w="100%">
        <Text
          fontSize="sm"
          color={lastUpdatedColor}
          textAlign="center"
        >
          {t('terms.lastUpdated')}: {new Date().toLocaleDateString()}
        </Text>
        <Heading
          as="h2"
          size={isBaseSize ? 'xl' : '2xl'}
          fontWeight="bold"
          color="brand.500"
          textAlign="center"
        >
          {t('terms.title')}
        </Heading>
        <VStack
          gap={6}
          alignItems="flex-start"
          w="100%"
          textAlign="left"
        >
          {sections.map((section) => (
            <Box key={section} w="100%">
              <Text
                fontSize={isBaseSize ? 'sm' : 'md'}
                color={textColor}
                lineHeight="1.8"
              >
                {t(`terms.sections.${section}`)}
              </Text>
            </Box>
          ))}
        </VStack>
      </VStack>
    </Flex>
  );
};

export default TermsOfService;

