import { Flex, Text, Link, useBreakpointValue } from '@chakra-ui/react';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useColorModeValue } from './ui/color-mode';

const Footer: React.FC = () => {
  const { t } = useTranslation();
  const isBaseSize = useBreakpointValue(
    { base: true, md: false },
    { ssr: false }
  );
  const year = new Date().getFullYear();
  const borderColor = useColorModeValue('#b8c8b5', '#9ca76e');

  return (
    <Flex
      w="100%"
      pt={16}
      pb={8}
      px={isBaseSize ? 4 : 12}
      alignItems="center"
      justifyContent="center"
      direction="column"
      gap={4}
      borderTop="1px solid"
      borderColor={borderColor}
    >
      <Flex
        direction={isBaseSize ? 'column' : 'row'}
        gap={4}
        alignItems="center"
        justifyContent="center"
      >
        <Link href="#terms" fontWeight="medium">
          {t('footer.termsLink')}
        </Link>
      </Flex>
      <Text fontSize={isBaseSize ? 'sm' : 'md'} textAlign="center">
        {t('footer.copyright', { year })}
      </Text>
    </Flex>
  );
};

export default Footer;

