import {
  Flex,
  Heading,
  Text,
  useBreakpointValue,
  SimpleGrid,
  Card,
  Box,
} from '@chakra-ui/react';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useColorModeValue } from './ui/color-mode';

const Features: React.FC = () => {
  const { t } = useTranslation();
  const isBaseSize = useBreakpointValue(
    { base: true, md: false },
    { ssr: false }
  );
  const cardBg = useColorModeValue('#E9F3DA', '#1e2210');
  const borderColor = useColorModeValue('#b8c8b5', '#9ca76e');
  const textColor = useColorModeValue('gray.700', 'gray.300');

  const features = [
    {
      key: 'routine',
      icon: '',
    },
    {
      key: 'notifications',
      icon: '',
    },
    {
      key: 'goals',
      icon: '',
    },
    {
      key: 'catchup',
      icon: '',
    },
    {
      key: 'privacy',
      icon: '',
    },
  ];

  return (
    <Flex
      id="features"
      w="100%"
      alignItems="center"
      justifyContent="center"
      px={isBaseSize ? 4 : 12}
      py={16}
      direction="column"
      gap={8}
    >
      <Heading
        as="h2"
        size={isBaseSize ? 'xl' : '2xl'}
        fontWeight="bold"
        color="brand.500"
        textAlign="center"
      >
        {t('features.title')}
      </Heading>
      <SimpleGrid
        columns={isBaseSize ? 1 : 3}
        gap={6}
        w="100%"
        maxW="1200px"
      >
        {features.map((feature) => (
          <Card.Root
            key={feature.key}
            bg={cardBg}
            border="2px solid"
            borderColor={borderColor}
            borderRadius="xl"
          >
            <Card.Body>
              <Box fontSize="4xl" mb={4} textAlign="center">
                {feature.icon}
              </Box>
              <Heading
                as="h3"
                size="md"
                fontWeight="bold"
                mb={2}
                color="brand.500"
              >
                {t(`features.${feature.key}.title`)}
              </Heading>
              <Text
                fontSize="sm"
                color={textColor}
              >
                {t(`features.${feature.key}.description`)}
              </Text>
            </Card.Body>
          </Card.Root>
        ))}
      </SimpleGrid>
    </Flex>
  );
};

export default Features;

